.buttons {
    display: flex;
    flex-wrap: wrap;
    }
    
    .buttons__button {
        background: transparent;
        color: teal;
        border: none;
        margin: 0 0 0 20px;
        transition: color 0.3s;
        cursor: pointer;
        
    }
    
    @media(max-width: 767px) {
        .buttons__button {
            flex-basis: 100%;
            margin: 10px;
        }
    }
    
    .buttons__button:hover {
        color: hsl(180, 100%, 35%);
        transition: 1s;
    }
    
    .buttons__button:disabled {
        color: rgb(182, 179, 179);
    }